import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import { ParallaxProvider } from "react-scroll-parallax"
import ParallaxBanner from "../components/parallaxBanner"

const VattenskadorPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            vattenskadorHeroes {
                hero {
                  url
                }
              }
        }
    }
  `)
    return (
        <Layout>
            <SEO title="Vattenskador" />
            <ParallaxProvider>
            <div style={{ display: "flex", flexDirection: "column" }}>
            <ParallaxBanner url={gcms.vattenskadorHeroes[0].hero.url} heading="Vattenskador" topMargin="-850px"/>
                <section style={{ background: "white", padding: "5vw 20vw"}}>
                <h1 style={{textAlign:"center"}}>Vattenskador</h1><br/>

                    <p>
                        Vi lokaliserar och åtgärdar alla typer av fuktskador i ditt golv, stora som små. Vi reparerar hela skadan genom besiktning, planering, rivning, och återställning.
                        Varje fuktskada är unik och därför ser varje reparation olika ut. Vi har god erfarenhet av vattenskador och jobbar med olika typer av fuktskyddssystem, bla genom:

                    </p>

                    <p>
                        Ventilerat golv - Att använda sig av ventilerade golv kan lösa många fuktproblem. Golvet installeras en bit ovanför en betongplatta vilket skapar en barriär mellan det kalla, fuktiga eller illaluktande underlaget.
                    </p>
                    <p>
                        Platon golv - Platonmattan monteras direkt ovanpå en betongplatta. Luftspalten som bildas mellan Platonmattan och betongen ventileras med inneluft. En fläkt ansluts och suger luften genom spalten.
                    </p>
                    <p>
                        Vi åtgärdar skadan med minimal påverkan för ditt boende.
                        
                    </p>
                </section>
            </div>
            </ParallaxProvider>
        </Layout>
    )
}

export default VattenskadorPage;